
import VueRouter from "vue-router";
// import designerRouter from "_platform/designer/router";
import designer from "../views/designer.vue";
import MobileDesigner from '../views/mobileDesigner.vue'

Vue.use(VueRouter);
//修正vue-router在跳转地址与当前路由相同的情况下报错

const routes = [
  //平台导出的默认路由
  ...$platformRouter.designer,
  //设计器路由地址
  {
    path: "/pc/page",
    name: "Designer",
    component: designer,
    meta: {
      title: "页面设计器"
    },
    props: route => {
      return {
        business: route.query.B,
        module: route.query.M,
        pageName: route.query.pname,
        pageTitle: route.query.title,
        mobile: false,
        tpl: route.query.tpl == 1 ? true : false
      };
    }
  },
  {
    path: "/mobile/page",
    name: "MobileDesigner",
    component: MobileDesigner,
    meta: {
      title: "页面设计器"
    },
    props: route => {
      return {
        business: route.query.B,
        module: route.query.M,
        pageName: route.query.pname,
        mobile: true,
        tpl: route.query.tpl == 1 ? true : false
      };
    }
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes
});

dsf.init.initRouter(router, 'pc');

export default router;