<template>
  <dsf-designer v-if="!isReload" model="design" v-bind="this.$attrs" />
</template>
<script>

export default {
  name: 'Designer',
  inheritAttrs: false,
  data() {
    return {
      isReload: false
    }
  },
  beforeCreate() {
  },
  created() {
  },
  mounted() {
    // console.log(Promise.all)
  },
  beforeRouteEnter(to, from, next) {
    if (from.name == to.name) {
      next((vm) => {
        vm.isReload = true;
        vm.$nextTick(() => {
          vm.isReload = false;
        })
      });
    }
    else {
      next((vm) => {
        vm.isReload = false;
      });
    }

  },
  beforeRouteUpdate(to, from, next) {
    this.isReload = true;
    this.$nextTick(() => {
      this.isReload = false;
    })
    next();
  }
}
</script>